import { isValidPhoneNumber, AsYouType} from 'libphonenumber-js';
import { writable } from 'svelte/store';

//Created tickets airline
const createCanvas = (
	name, lastname, dni,
	number, destination, dateTravel) => {
	const form = new FormData();
	form.append('number', number);
  const canvas = document.createElement('canvas');
  canvas.width = 350;
  canvas.height = 210;
  const ctx = canvas.getContext('2d');
  ctx.fillStyle = '#222';
  ctx.fillRect(0, 0, canvas.width, canvas.height);
  ctx.fillStyle = '#fff';
  ctx.font = '2rem Arial';
  ctx.fillText('Ticket De Viaje', 10, 30);
  ctx.font = '1.2rem Arial';
  ctx.fillText(`Pasajero: ${name} ${lastname}`, 10, 70);
  ctx.fillText(`DNI: ${dni}`, 10, 100);
  ctx.fillText(`Destino: ${destination}`, 10, 130);
  ctx.fillText(`Fecha de viaje: ${dateTravel}`, 10, 160);
  ctx.fillText(`Telefono: ${number}`, 10, 190);

  canvas.toBlob(blob => {
    let img = blob;
		form.append('blob', img);
		fetch('https://data-message.deno.dev/data/url', {
			method: 'POST',
			body: form
		})
  }, 'image/png');
}

// Stores for the form
export const stateReserve = writable('awaiting');

//Send Information a Server
const sendInfo = async (e) => {
	e.preventDefault();
	const all = [];
	const smallArr = [];
	const names = [];
	const lastnames = [];
	const dnis = [];
	const primitiveData = {};
	const form = new FormData(e.target);

	const regexDate = /^(?:3[01]|[12][0-9]|0?[1-9])([\-/.])(0?[1-9]|1[1-2])\1\d{4}$/;
	const regex = /^[a-z\sA-Z]*$/;
	const regexCant = /^[0-9]{1}$/;
	const regexDni = /^[0-9]{8}$/;
	const regexEmail = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
	for (const [key, value] of form.entries()) {
		if (key === 'destiny' && value.length > 0 && regex.test(value)) {
			all.push(value);
			primitiveData.destiny = value;
		}
		if (key === 'dateTravel' && value.length > 0 && regexDate.test(value)) {
			all.push(value);
			primitiveData.dateTravel = value;
		}
		if (key === 'pasajeros' && value.length > 0 && regexCant.test(value)) all.push(value);
		if (key === 'name' && value.length > 0 && regex.test(value)) {
			all.push(value);
			names.push(value);
		}
		if (key === 'lastName' && value.length > 0 && regex.test(value)) {
			all.push(value);
			lastnames.push(value);
		}
		if (key === 'dni' && value.length < 99999999 && regexDni.test(value)) {
			all.push(value);
			dnis.push(value);
		}
		if (key === 'nacionality' && value.length > 0 && regex.test(value)) all.push(value);
		if (key === 'dateTime' && value.length > 0 && regexDate.test(value)) all.push(value);
		if (key === 'email' && value.length > 0 && regexEmail.test(value)) all.push(value);
		if (key === 'number' && value.length > 0 && isValidPhoneNumber(value, 'AR')) {
			all.push(value);
			primitiveData.number = value;
		}
		smallArr.push(value);
	}

	if (all.length === smallArr.length) {
		stateReserve.set('');
		names.forEach((name, index) => {
			createCanvas(name, lastnames[index], dnis[index], primitiveData.number, primitiveData.destiny, primitiveData.dateTravel);
		});
	}
}

//Validation Destination
const validateDestination = node => {
	const regex = /^[a-z\sA-Z]*$/;
  let err = node.target.parentNode;
	if (node.target.value.length > 0 && regex.test(node.target.value)) {
    return err.id = '';
	};
  return err.id = 'error';
}

//Validate Name
const validateName = node => {
  const regex = /^[a-z\sA-Z]*$/;
  let err = node.target.parentNode;
  if (node.target.value.length > 0 && regex.test(node.target.value)) {
    err.id = '';
    return true;
  };
  return err.id = 'error';
}


//Validation Date Travel
const validateDateTravel = node => {
	const regex = /^(?:3[01]|[12][0-9]|0?[1-9])([\-/.])(0?[1-9]|1[1-2])\1\d{4}$/;
  let err = node.target.parentNode;
	if (node.target.value.length > 0 && regex.test(node.target.value)) {
		err.id = '';
		return true;
	};
	return err.id = 'error';
}

//Validation Cantity of people
export const cantityPeople = writable(1);
const validateCantity = (node) => {
	const regex = /^[0-9]{1}$/;
  let err = node.target.parentNode;
	if (regex.test(node.target.value) && Number(node.target.value) !== 0 && Number(node.target.value) < 7) {
		cantityPeople.set(node.target.value);
		err.id = '';
		return true;
	};
	cantityPeople.set(1);
	return err.id = 'error';
}

//Validation document number
const validateDocument = node => {
	const regex = /^[0-9]{8}$/;
	let err = node.target.parentNode;
	if (node.target.value.length < 99999999 && regex.test(node.target.value)) {
		err.id = '';
		return true;
	};
	return err.id = 'error';
}

//Validation nacionality
const validateNacionality = node => {
	const regex = /^[a-z\sA-Z]*$/;
	let err = node.target.parentNode;
	if (node.target.value.length > 0 && regex.test(node.target.value)) {
		err.id = '';
		return true;
	};
	return err.id = 'error';
}

//Validation Date of Birth
const validateDateOfBirth = node => {
	const regex = /^(?:3[01]|[12][0-9]|0?[1-9])([\-/.])(0?[1-9]|1[1-2])\1\d{4}$/;
	let err = node.target.parentNode;
	if ( node.target.value.length > 0 && regex.test(node.target.value)) {
		err.id = '';
		return true;
	};
	return err.id = 'error';
}

//Validation Email
const validateEmail = node => {
	const regex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
	let err = node.target.parentNode;
	if (node.target.value.length > 0 && regex.test(node.target.value)) {
		err.id = '';
		return true;
	};
	return err.id = 'error';
}

//Validation Phone
const validatePhone = (node) => {
  node.target.value = new AsYouType('AR').input(node.target.value);
  let err = node.target.parentNode;
  if (isValidPhoneNumber(node.target.value, 'AR')) {
    err.id = '';
    return true;
  }
  return err.id = 'error';
};


export {
	sendInfo,
	validateDestination,
	validateName,
	validateDateTravel,
	validateCantity,
	validateDocument,
	validateNacionality,
	validateDateOfBirth,
	validateEmail,
	validatePhone,
}
