<script lang='ts'>
  import SliderIcon from './content/SliderIcon.svelte';

  let idImg: number = 0;
  const num = (): void => {
    idImg = idImg + 1;
    if (idImg > 3) idImg = 0;
  }

  let state: ReturnType<typeof setInterval>;
  let element: HTMLDivElement;
  let addObersver: IntersectionObserver;

  const stateSlide = (entries: Array<IntersectionObserverEntry>) => {
    entries.map(entry => {
      if (entry.isIntersecting) return state = setInterval(num, 4000)
      clearInterval(state);
    })
  }

  const startup = () => {
    let observerOptions = {
      root: null,
      rootMargin: '0px',
      threshold: 1.0,
    }
    addObersver = new IntersectionObserver(stateSlide, observerOptions);
    addObersver.observe(element);
  }

  window.addEventListener('load', startup, false);
</script>

<script context='module'>
  import { writable } from 'svelte/store';
  export const states = writable('');
</script>

<div bind:this={element} class='box-img'>
  {#if $states === '/'}
    <img src='/img/slider-home/{idImg}.png' alt='logo-page'>
    <div class='item-box'>
      <SliderIcon nameIcon='circulo-floripa2019-min'/>
      <SliderIcon nameIcon='circulo-ferrugem2019-min'/>
      <SliderIcon nameIcon='circulo-arenamoon-min'/>
    </div>
  {:else if $states === '/argentina'}
    <img src='/img/slider-home/{$states}-min.jpg' alt='logo page {$states}'>
  {:else if $states === '/bolivia'}
    <img src='/img/slider-home/{$states}-min.jpg' alt='logo page {$states}'>
  {:else if $states === '/brasil'}
    <img src='/img/slider-home/{$states}-min.jpg' alt='logo page {$states}'>
  {:else if $states === '/fest' || $states === '/reserve'}
    <img src='/img/slider-home/fest-min.jpg' alt='logo page {$states}'>
  {/if}
</div>

<style>
  .box-img {
    position: relative;
    z-index: 0;
  }

  .item-box {
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    bottom: -8.7em;
  }

  img {
    display: block;
    height: 37.7em;
    object-fit: cover;
    width: 100%;
  }
</style>
