<script context="module">
  let tag = document.createElement("script");
  tag.src = "https://www.youtube.com/iframe_api";
  let firstScriptTag = document.getElementsByTagName("script")[0];
  firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

  window.onYouTubeIframeAPIReady = () =>
    window.dispatchEvent(new Event("iframeApiReady"));
</script>

<script>
  export let videoId;
  import Lazy from 'svelte-lazy';
</script>

<Lazy width='99%'>
  <iframe src="https://www.youtube.com/embed/{videoId}?enablejsapi=1&origin=http%3A%2F%2Flocalhost%3A8080&widgetid=1" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
</Lazy>

<style>

  iframe {
    width: 100%;
    height: 550px;
  }

  @media (max-width: 623px) {
    iframe {
      width: 100%;
      height: 300px;
    }
  }
</style>
