<script>
  import {Link} from 'svelte-navigator';
  import Lazy from 'svelte-lazy';
  import ProductInfo from './ProductInfo.svelte';
  import ProductFest from './ProductFest.svelte';
  import ProductReserve from './ProductReserve.svelte';
  export let country;
  const data = async () => {
    const services = await fetch('https://apiverify.deno.dev/data/products');
    const service = await services.json();
    return service;
  }

  let promise = data();
</script>

<article class='box-prices'>
  {#if country === '/' }
    <h2><strong>PROMOCIONES IMPERDIBLES</strong></h2>
  {/if}
  {#if country == '/bolivia'}
    <div class='item-prices'>
      <Lazy>
        <Link to='/travels/lapaz'>
          <ProductInfo nameProduct='14-LaPaz-5-min'/>
        </Link>
      </Lazy>
    </div>
  {:else if country == '/fest'}
    <div class='item-prices'>
      {#await promise then services}
        {#each services as {src, nameEvent, lapseEvent, descriptionEvent}}
          <Lazy height={300} offset={10}>
            <ProductFest {src} {nameEvent} {lapseEvent} {descriptionEvent}/>
          </Lazy>
        {/each}
      {/await}
    </div>
  {:else if country === '/reserve'}
    <ProductReserve state='awaiting'/>
  {:else if country === '/' || country === '/argentina'}
    <div class='item-prices'>
      <Lazy height={300} offset={0}>
        <Link to='/travels/bariloche'>
          <ProductInfo nameProduct='13-BarilocheVeranoFest-5-min'/>
        </Link>
      </Lazy>
      <Lazy height={300} offset={0}>
        <Link to='/travels/cataratas'>
          <ProductInfo nameProduct='41-CAtaratas-1-min'/>
        </Link>
      </Lazy>
      <Lazy height={300} offset={0}>
        <Link to='/travels/jujuy'>
          <ProductInfo nameProduct='42-Jujuy-4-min'/>
        </Link>
      </Lazy>
    </div>
  {/if}
</article>

