<script>
  import InputReserve from './InputReserve.svelte';
  import AirplaneLanding from 'svelte-material-icons/AirplaneLanding.svelte'
  import Calendar from 'svelte-material-icons/Calendar.svelte';
  import AccountGroup from 'svelte-material-icons/AccountGroup.svelte';
  import Account from 'svelte-material-icons/Account.svelte';
  import CardAccountDetails from 'svelte-material-icons/CardAccountDetails.svelte';
  import FileDocument from 'svelte-material-icons/FileDocument.svelte';
  import CakeVariant from 'svelte-material-icons/CakeVariant.svelte';
  import Email from 'svelte-material-icons/Email.svelte';
  import CellPhone from 'svelte-material-icons/CellphoneCheck.svelte';
  import AirplaneCheck from 'svelte-material-icons/AirplaneCheck.svelte'
  import {
    sendInfo,
    validateDateTravel,
    validateCantity,
    validateName,
    validateDocument,
    validateNacionality,
    validateDateOfBirth,
    validateEmail,
    validatePhone,
    cantityPeople,
    stateReserve,
  } from './sendForm.js';
  import AutoComlete from './AutoComlete.svelte';
  let passengers;
  $: passengers = new Array(Number($cantityPeople));
  export let state;
  $stateReserve = state;
</script>
{#if $stateReserve === 'awaiting'}
<form on:submit={sendInfo}>
  <label for='destiny'>DONDE Y CUANDO VIAJAN?</label>
  <AutoComlete>
    <AirplaneLanding slot='icon' size={27}/>
  </AutoComlete>
  <InputReserve name='dateTravel' place='FECHA DE VIAJE' on:keyup={validateDateTravel}>
    <Calendar slot='icon' size={27}/>
  </InputReserve>
  <label for='pasajeros'>CUANTOS PASAJEROS</label>
  <InputReserve type='number' name='pasajeros' place='SELECCIONE LOS PASAJEROS' on:keyup={node => validateCantity(node)} def={1}>
    <AccountGroup slot='icon' size={27}/>
  </InputReserve>
  <label for='datosPersonales'>DATOS PERSONALES (TAL CUAL FIGURE EN EL DOCUMENTO DE VIAJE)</label>
  {#each passengers as p, id}
    <span>Pasajero {id + 1}</span>
    <InputReserve name='name' place='NOMBRE' on:keyup={validateName}>
      <Account slot='icon' size={27}/>
    </InputReserve>
    <InputReserve name='lastName' place='APELLIDO' on:keyup={validateName}>
      <Account slot='icon' size={27}/>
    </InputReserve>
    <InputReserve type='number' name='dni' place='DNI' on:keyup={validateDocument}>
      <CardAccountDetails slot='icon' size={27}/>
    </InputReserve>
    <InputReserve name='nacionality' place='NACIONALIDAD' on:keyup={validateNacionality}>
      <FileDocument slot='icon' size={27}/>
    </InputReserve>
    <InputReserve name='dateTime' place='FECHA DE NACIMIENTO' on:keyup={validateDateOfBirth}>
      <CakeVariant slot='icon' size={27}/>
    </InputReserve>
  {/each}
  <label for='contact'>DATOS DE CONTACTO</label>
  <InputReserve type='email' name='email' place='E-MAIL' on:keyup={validateEmail}>
    <Email slot='icon' size={27}/>
  </InputReserve>
  <InputReserve name='number' place='TELEFONO' on:keyup={validatePhone}>
    <CellPhone slot='icon' size={27}/>
  </InputReserve>
  <button>ENVIAR FORMULARIO</button>
</form>
{:else}
  <article>
