    <p>Su reserva ha sido completada con éxito, en 2 o 5 minutos recivira un mensaje con los datos y un ticket con sus datos de reserva, muchas gracias por elegirnos.</p>
    <AirplaneCheck slot='icon' size={270} color='gray'/>
  </article>
{/if}

