<script lang='ts'>
  import {validateDestination} from './sendForm.js';
  let country: Array<any>;
  (async () => {
    const countryDestination = await fetch('https://restcountries.com/v3.1/all');
    country = await countryDestination.json();
  })();

  let countryList: Array<any> = [];
  const autoComplete = node => {
    const search = node.target.value;
    countryList = country.filter(item => {
      return item.name.common.toLowerCase().startsWith(search.toLowerCase());
    })
    cls = 'search';
  }
  let cls: string;
  let value: string;
  const handleSelect = (e) => {
    value = e.target.textContent;
    cls = 'hidden';
  }

  let hid: string = '';
</script>

<div id={hid}>
  <input type='text' name='destiny' placeholder='DESTINO' bind:value={value} on:keyup={autoComplete} on:keyup={validateDestination} autocomplete='off'>
  <span><slot name='icon'></slot></span>
  {#if countryList.length > 0}
  <ul class={cls}>
  {#each countryList as {name: {common}}}
    <li on:click={handleSelect}>{common}</li>
  {/each}
  </ul>
  {/if}
</div>

